import React from "react";
import {
	Box,
	styled,
	Typography,
	Button,
	Grid,
	Paper,
	Container,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";

const style = {
	position: "fixed",
	top: "50%",
	left: "50%",
	transform: "translate(-50%, -50%)",
	width: "60%",
	height: "60%",
	backgroundColor: "#fff",
	zIndex: "1000",
	overflow: "scroll",
};

const StyledHeaderBox = styled(Box)(({ theme }) => ({
	display: "flex",
	background: "red",
	color: "#fff",
	height: "10vh",
	alignItems: "center",
}));

const StyledContainer = styled(Container)(({ theme }) => ({
	margin: "0 auto",
}));
const StyledTitle = styled(Typography)(({ theme }) => ({
	margin: "1vh 0",
}));
const StyledSubTitle = styled(Typography)(({ theme }) => ({
	fontWeight: "bold",
}));
const StyledGrid = styled(Grid)(({ theme }) => ({
	//   margin: '1vw'
}));

// const Item = styled(Paper)(({ theme }) => ({
// 	...theme.typography.body2,
// 	padding: theme.spacing(1),
// 	textAlign: "center",
// 	color: theme.palette.text.secondary,
// }));

const ImgCheckList = ({ handleClose }) => {
	const handleclick = () => {
		handleClose();
	};
	return (
		<div>
			<Box sx={style}>
				<StyledHeaderBox>
					<Button
						onClick={handleclick}
						sx={{ position: "absolute", color: "#fff" }}
					>
						<CloseIcon fontSize="large" />
					</Button>
					<Typography
						variant="h5"
						color=""
						sx={{ textAlign: "center", width: "100%" }}
					>
						   
					</Typography>
				</StyledHeaderBox>
				<StyledContainer>
					<Typography
						id="modal-modal-title"
						variant="h6"
						component="h2"
						sx={{ margin: "5vh 0" }}
					>
						     .
					</Typography>
					<StyledGrid container spacing={2} columns={25} direction="column">
						<Grid item xs={5}>
							<StyledTitle variant="h5">1.</StyledTitle>
							<Typography>
								   150mm ~ 600mm    
								  30  .
							</Typography>
						</Grid>
						<Grid item xs={5}>
							<StyledTitle variant="h5">2. - </StyledTitle>
							<Typography>
								     150mm ~ 600mm 
								       
								 .
							</Typography>
							<StyledSubTitle>a.: 4x3, 1920x1080</StyledSubTitle>
							<StyledSubTitle>b. : 1.2M </StyledSubTitle>
							<StyledSubTitle>c.(): 500Lux(lx) </StyledSubTitle>
							<StyledSubTitle>
								d.:   90  
							</StyledSubTitle>
							<StyledSubTitle>
								#        
								 .
							</StyledSubTitle>
						</Grid>
						<Grid item xs={5}>
							<StyledTitle variant="h5">3.</StyledTitle>
							<Typography>
								     4(090180270) 
								        
								.
							</Typography>
						</Grid>
						<Grid item xs={5}>
							<StyledTitle variant="h5">4.</StyledTitle>
							<Typography>
								        
								.
							</Typography>
							<StyledSubTitle>a.,</StyledSubTitle>
							<StyledSubTitle>b. ,  </StyledSubTitle>
							<StyledSubTitle>c. </StyledSubTitle>
							<StyledSubTitle>d. </StyledSubTitle>
							<StyledSubTitle>e.</StyledSubTitle>
						</Grid>
						<StyledGrid item xs={5}>
							<StyledTitle variant="h5">
								5.    
							</StyledTitle>
							<Typography></Typography>
						</StyledGrid>
					</StyledGrid>
				</StyledContainer>
			</Box>
		</div>
	);
};

export default ImgCheckList;
